<template>
  <div class="cart">
    <div class="cart-title">购物车</div>
    <div class="cart__con"
         v-for="(v,key,i) in cartGoods"
         :key="v.goodsId">
      <van-checkbox @change="selectOne(v)"
                    v-model="checked[i]"></van-checkbox>
      <van-card class="cart-area"
                :num="v.num"
                :price="v.Price"
                :desc="v.desc"
                :title="v.title"
                :thumb="v.topImages">
        <template v-slot:title>
          <div class="cart__item-top">{{ v.title }}</div>
        </template>
        <template v-slot:desc>
          <div class="cart__item-bottom">{{ v.detail }}</div>
        </template>
      </van-card>
    </div>
    <van-submit-bar :price="this.sum"
                    button-text="提交订单">
      <van-checkbox v-model="selectAll"
                    @change="E_selectAll">全选</van-checkbox>
    </van-submit-bar>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'cart',
  data () {
    return {
      checked: [],
      selectAll: true,
      sum: 0
    }
  },
  methods: {
    E_selectAll (newVal) {
      const item = this.checked.every((v) => {
        return v === true
      })
      if (!newVal && item) {
        this.checked = this.checked.map(() => {
          return newVal
        })
      }
      if (newVal) {
        this.checked = this.checked.map(() => {
          return newVal
        })
      }
    },
    selectOne () {
      let i = 0
      let sum = 0
      for (const key in this.cartGoods) {
        if (this.checked[i]) {
          sum += parseInt(this.cartGoods[key].Price) * this.cartGoods[key].num
        }
        i++
      }
      const item = this.checked.every((v) => {
        return v === true
      })

      if (item) {
        this.selectAll = true
      } else {
        this.selectAll = false
      }
      this.sum = sum * 100
      console.log(sum)
    }
  },
  computed: {
    price () {
      return this.checked
    },
    ...mapState({
      cartGoods: 'cartGoodsData'
    })
  },
  created () {
    const length = Object.keys(this.cartGoods).length
    for (let i = 0; i < length; i++) {
      this.checked.push(true)
    }
    this.E_selectAll()
    this.selectOne()
  }
}
</script>
<style lang="scss" scoped>
.cart {
  font-size: 0.3rem;
}
.cart-title {
  height: 50px;
  font-size: 0.4rem;
  line-height: 50px;
  text-align: center;
  color: white;
  background: #ff8198;
}
.cart__con {
  display: flex;
  background: #fff;
}
.cart-area {
  flex: auto;
}
.cart__item-top,
.cart__item-bottom {
  width: 55vw;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.cart__item-bottom {
  margin-top: 5px;
}
.van-submit-bar {
  margin-bottom: 55px;
}
</style>
